.banner-wrap {
	padding-top: 20px;
	margin-bottom: 16px;
	@include media-breakpoint-down(sm) {
		padding-top: 5px;
		margin-bottom: 15px; } }
.home-banner {
	@include media-breakpoint-down(sm) {
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 3px 20px color-rgba($black,0.1); }
	.slick-prev, .slick-next {
		@include pos(abs, 50% null null null);
		@include transform(translateY(-50%));
		z-index: 1;
		@include fs(0);
		color: $white;
		text-shadow: 0px 1px 5px color-rgba($black,0.1);
		opacity: 0;
		@include transition(all,0.5s);
		&:before {
			content: '';
			font-family: 'linearicons-free';
			@include fs(28rem); } }
	.slick-prev {
		left: 0px;
		&:before {
			content: '\e875'; } }
	.slick-next {
		right: 0px;
		&:before {
			content: '\e876'; } }
	&:hover {
		.slick-prev, .slick-next {
			opacity: 1; }
		.slick-prev {
			left: 20px; }
		.slick-next {
			right: 20px; } }
	.slick-dots {
		text-align: center;
		@include pos(abs, null 0 15px 0);
		li {
			display: inline-block;
			width: 10px;
			height: 10px;
			@include bor-rad(50px);
			background: none;
			border: 1px solid $white;
			@include pos(rel,null);
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 6px;
				height: 6px;
				@include pos(abs,1px null null 1px);
				background: $lblue;
				@include bor-rad(50px);
				opacity: 0; }
			+ li {
				margin-left: 5px; }
			button {
				display: none; }
			&.slick-active {
				border-color: $lblue;
				&:before {
					opacity: 1; } } } }
	.item {
		.banner-img {
			display: block;
			@include pos(rel,null);
			&:after {
				content: '';
				@include pos(abs, 75% 0 0 0);
				z-index: 3;
				background: linear-gradient(color-rgba($black,0),color-rgba($black,0.3)); }
			img {
				width: 100%;
				@include media-breakpoint-down(sm) {
					height: 200px;
					object-fit: cover; } }
			.caption {
				@include pos(abs, null null 40px 50%);
				@include transform(translateX(-50%));
				text-align: center;
				z-index: 5;
				width: 100%;
				max-width: 720px;
				@include fs(48rem);
				line-height: 1.3;
				font-weight: 200;
				letter-spacing: 1px;
				color: $white;
				text-shadow: 2px 3px 5px color-rgba($black,0.3);
				@include media-breakpoint-down(lg) {
					@include fs(32rem);
					max-width: 550px; }
				@include media-breakpoint-down(md) {
					@include fs(28rem);
					max-width: 500px;
					bottom: 30px; }
				@include media-breakpoint-down(sm) {
					@include fs(18rem);
					bottom: 15px; }
				strong {
					font-weight: 500; } } } } }
.slick-slide {
	.item {
		display: block !important; } }
