body {
	padding-top: 170px;
	@include media-breakpoint-down(lg) {
		padding-top: 0;
		.fullpage {
			padding-top: 50px; } } }
header {
	background: $white;
	@include pos(fix, 0 0 null 0);
	z-index: 1000;
	box-shadow: 0px 0px 50px color-rgba($black,0.05);
	@include media-breakpoint-down(lg) {
		background: linear-gradient(color-rgba($white,1),color-rgba($white,0));
		box-shadow: none; }
	&.minimal {
		@include media-breakpoint-down(lg) {
			background: $white; } }
	.header-top {
		background: $blue;
		color: $white;
		@include media-breakpoint-down(lg) {
			display: none; } }
	.header-wrap {
		@include flexbox;
		align-items: center;
		align-content: center; }
	.col-right {
		flex: auto;
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: flex-end; }
	.top-link, .hotline, .help, .language {
		@include flexbox;
		align-items: center;
		align-content: center;
		height: 35px;
		a {
			@include flexbox;
			align-items: center;
			align-content: center;
			@include fs(13rem);
			font-weight: 300;
			// text-transform: uppercase
			height: 20px;
			.material-icons, img {
				@include fs(16rem);
				margin-right: 5px;
				color: $lblue;
				opacity: 0.8; } } }
	.help {
		margin-left: 15px;
		a {
			border-left: 1px solid color-rgba($white,0.2);
			padding-left: 15px; } }
	.header-main {}
	.btn-showmenu {
		display: none;
		@include media-breakpoint-down(lg) {
			@include flexbox;
			margin-right: 10px;
			color: $white;
			width: 30px;
			height: 30px;
			@include bor-rad(3px);
			background: $blue;
			align-items: center;
			align-content: center;
			justify-content: center;
			.material-icons {
				@include fs(18rem); } } }
	.overlay {
		display: none;
		@include pos(fix, 0 null 0 0);
		right: 0;
		z-index: 500;
		background: $black;
		opacity: 0.6; }
	.sitelogo {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		height: 85px;
		@include media-breakpoint-down(lg) {
			height: 50px;
			@include transform(translateY(-3px));
			img {
				height: 20px; } } }
	.search {
		margin-left: 80px;
		@include media-breakpoint-down(lg) {
			margin-left: 0;
			@include pos(fix, 50px 15px null null);
			z-index: 50;
			display: none; }
		.searchbox {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			width: 510px;
			@include bor-rad(50px);
			overflow: hidden;
			border: 2px solid darken($white,5%);
			padding: 2px;
			@include media-breakpoint-down(lg) {
				width: 200px;
				@include bor-rad(5px);
				background: $white;
				border: none;
				box-shadow: 0px 3px 10px color-rgba($black,0.05); }
			input {
				width: calc(100% - 36px);
				border: none;
				background: none;
				@include fs(13rem);
				padding: 0px 20px;
				@include media-breakpoint-down(lg) {
					padding: 0px 10px; }
				&::-webkit-input-placeholder {
					opacity: 0.5;
					font-style: italic; } }
			.searchbutton {
				width: 32px;
				height: 32px;
				@include bor-rad(50%);
				background: $lblue;
				color: $white;
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				@include media-breakpoint-down(lg) {
					width: 32px;
					height: 32px;
					background: none;
					color: $lblue;
					.mi {
						@include fs(20rem); } } } } }
	.login {
		a {
			@include flexbox;
			align-items: center;
			align-content: center;
			@include fs(13rem);
			.material-icons {
				color: $blue;
				@include fs(36rem);
				@include media-breakpoint-down(lg) {
					@include fs(22rem); } }
			.text {
				line-height: 1.4;
				@include media-breakpoint-down(lg) {
					display: none; }
				strong {
					display: block;
					font-weight: 600; } } } }
	.cart {
		margin-left: 30px;
		@include media-breakpoint-down(lg) {
			margin-left: 15px; }
		.cart-toggle {
			@include bor-rad(50px);
			border: 2px solid $lblue;
			padding: 0px 25px;
			@include flexbox;
			line-height: 34px;
			align-items: center;
			align-content: center;
			@include fs(14rem);
			@include pos(rel,null);
			@include media-breakpoint-down(lg) {
				@include fs(0);
				margin: 0;
				padding: 0;
				border: none;
				line-height: 1; }
			.material-icons {
				color: $lblue;
				@include fs(18rem);
				margin-right: 15px;
				@include media-breakpoint-down(lg) {
					@include fs(18rem);
					color: $blue;
					margin-right: 0; } }
			.cart-amount {
				color: $blue;
				font-weight: 700;
				margin-left: 5px;
				@include media-breakpoint-down(lg) {
					background: $amber;
					color: $white;
					@include fs(10rem);
					line-height: 1;
					padding: 1px;
					@include bor-rad(100px);
					@include pos(abs, -5px -10px null null);
					z-index: 1; } } } }
	.search-toggle {
		display: none;
		@include media-breakpoint-down(lg) {
			width: 26px;
			height: 26px;
			@include bor-rad(50px);
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			color: $white;
			background: $lblue;
			margin-left: 15px;
			.material-icons {
				@include fs(18rem); } } }
	.header-menu {
		border-top: 1px solid darken($white,5%);
		@include media-breakpoint-down(lg) {
			// display: none
			@include pos(fix, 0 null 0 0);
			width: 280px;
			z-index: 1000;
			background: $white;
			@include transform(translateX(-100%));
			@include transition(all,0.5s);
			&.open {
				@include transform(translateX(0)); }
			.container {
				padding: 0; } } }
	.menu-wrap {
		@include flexbox;
		flex-wrap: wrap;
		align-items: center;
		align-content: center;
		@include pos(rel,null);
		@include media-breakpoint-down(lg) {
			display: block;
			border-top: 4px solid $lblue; }
		.menu-title {
			background: $lblue;
			color: $white;
			height: 50px;
			padding: 0px 20px;
			@include flexbox;
			align-items: center;
			align-content: center;
			cursor: pointer;
			@include media-breakpoint-down(lg) {
				display: none; }
			.material-icons {
				margin-right: 15px; }
			.text {
				@include fs(14rem);
				font-weight: 500;
				text-transform: uppercase; } }
		.menu1 {
			width: 290px;
			@include media-breakpoint-down(lg) {
				width: auto;
				border-bottom: 1px solid darken($white,5%);
				margin: 0px 15px;
				padding: 15px 0; }
			ul.menu-link {
				@include pos(abs, 100% null null 0);
				background: $white;
				visibility: hidden;
				@include opacity(0);
				@include transition(all,0.5s);
				width: 290px;
				box-shadow: 0px 5px 20px color-rgba($black,0.2);
				@include media-breakpoint-down(lg) {
					width: auto;
					@include pos(static,null);
					box-shadow: none;
					visibility: visible;
					@include opacity(1); }
				> li {
					width: 100%;
					@include media-breakpoint-down(lg) {
						padding: 0px; }
					> a {
						font-weight: normal;
						text-transform: uppercase;
						border-bottom: 1px solid darken($white,5%);
						@include pos(rel,null);
						@include media-breakpoint-down(lg) {
							border-bottom: none; }
						&:after {
							content: '';
							display: block;
							border-top: 10px solid transparent;
							border-bottom: 10px solid transparent;
							border-right: 10px solid $white;
							@include pos(abs,15px -20px null null);
							display: none;
							z-index: 105; }
						.text {
							font-weight: normal; } }
					&.has-mega {
						.mega {
							@include pos(abs, 0 null null 100%);
							background: $white;
							z-index: 100;
							width: calc(1250px - 290px);
							min-height: 100%;
							padding: 20px;
							box-shadow: 0px 5px 20px color-rgba($black,0.2);
							display: none;
							.mega-col {
								width: 50%;
								+ .mega-col {
									border-left: 1px solid darken($white,10%); }
								.menu-banner {
									padding-left: 20px; }
								ul {
									width: 50%;
									height: 100%;
									@include pos(rel,null);
									// border-right: 1px solid darken($white,10%)
									li {
										padding: 0px 20px;
										a {
											display: block;
											@include fs(14rem);
											line-height: 1.3;
											font-weight: 400;
											text-transform: uppercase;
											padding: 10px 0;
											padding-left: 20px;
											@include pos(rel,null);
											&:before {
												content: '\f105';
												font-family: 'fontawesome';
												@include pos(abs, 10px null null 0); } }
										.mega-sub {
											@include pos(abs, 0 null null 100%);
											width: 100%;
											height: 100%;
											border-left: 1px solid darken($white,10%);
											display: none;
											ul {
												width: 100%;
 } }												// border-right: none
										&:hover {
											> a {
												color: $lblue; }
											.mega-sub {
												display: block; } } } } } }
						&:hover {
							@include media-breakpoint-up(xl) {
								a {
									&:after {
										display: block; } }
								.mega {
									@include flexbox;
									flex-wrap: wrap; } } } } } }

			&:hover {
				@include media-breakpoint-up(xl) {
					ul.menu-link {
						visibility: visible;
						@include opacity(1); } } } }
		.menu2 {
			@include media-breakpoint-down(lg) {
				padding: 15px 0; } }
		ul.menu-link {
			@include flexbox;
			align-items: center;
			align-content: center;
			flex-wrap: wrap;
			> li {
				padding: 0px 20px;
				> a {
					@include flexbox;
					align-items: center;
					align-content: center;
					@include fs(14rem);
					font-weight: 500;
					.icon {
						width: 32px;
						height: 32px;
						@include flexbox;
						align-items: center;
						align-content: center;
						justify-content: center;
						margin-right: 15px;
						@include media-breakpoint-down(lg) {
							width: 24px;
							height: 24px;
							margin-right: 10px;
							img {
								max-width: 100%;
								max-height: 100%; } } }
					.text {
						line-height: 20px;
						padding: 15px 0;
						font-weight: 500;
						@include media-breakpoint-down(lg) {
							padding: 10px 0; } } }
				&:hover {
					> a {
						color: $lblue; } } } } } }
