.product-page-wrap {
	background: $white;
	margin: 20px 0;
	@include flexbox;
	.product-filter {}
	.product-page {
		width: calc(100% - 240px);
		@include media-breakpoint-down(md) {
			width: 100%; } }
	.btn-showfilter {
		display: none;
		@include media-breakpoint-down(md) {
			display: inline-block;
			@include bor-rad(50px);
			padding: 5px 15px;
			@include fs(14rem);
			color: $lblue;
			border: 1px solid $lblue;
			.mi {
				@include fs(18rem);
				vertical-align: middle;
				margin-right: 10px; } }
		@include media-breakpoint-down(sm) {
			margin: 10px 15px;
			display: block;
			@include pos(fix, null 0 0 0);
			z-index: 500;
			text-align: center;
			background: $white;
			&:hover {
				background: $lblue;
				color: $white; } } } }

.product-page {
	background: $white;
	padding: 35px;
	@include pos(rel,null);
	@include media-breakpoint-down(sm) {
		padding: 15px; } }

.product-sort {
	@include flexbox;
	align-items: center;
	align-content: center;
	@include pos(abs, 35px 35px null null);
	@include media-breakpoint-down(sm) {
		@include pos(static,null);
		margin-top: -10px;
		margin-bottom: 20px; }
	select {
		@include bor-rad(50px);
		padding: 5px 15px; }
	.change-view {
		@include flexbox;
		align-items: center;
		align-content: center;
		margin-left: 20px;
		a {
			display: block;
			opacity: 0.2;
			.mi {
				display: block; }
			&.active {
				opacity: 1;
				color: $lblue; } } } }
