.cart-page {
	margin: 20px 0;
	background: $white;
	padding: 35px;
	@include media-breakpoint-down(sm) {
		padding: 15px; } }

.address-list {
	.item {
		margin-bottom: 20px;
		.address {
			padding: 30px;
			@include bor-rad(10px);
			@include pos(rel,null);
			border: 1px solid darken($white,10%);
			@include transition(all,0.5s);
			@include media-breakpoint-down(sm) {
				padding: 15px; }
			&:hover {
				border-color: transparent;
				box-shadow: 0px 5px 20px color-rgba($black,0.2); }
			.name {
				@include fs(24rem);
				font-weight: 500;
				margin-bottom: 10px;
				@include media-breakpoint-down(sm) {
					@include fs(16rem); } }
			.phone {
				@include fs(18rem);
				margin-bottom: 10px;
				@include media-breakpoint-down(sm) {
					@include fs(14rem); } }
			.addr {
				@include fs(16rem);
				opacity: 0.6;
				@include media-breakpoint-down(sm) {
					@include fs(14rem); } }
			.default {
				display: inline-block;
				color: $lblue;
				@include fs(15rem);
				@include bor-rad(100px);
				border: 1px solid $lblue;
				padding: 0px 10px;
				margin-top: 10px; }
			.btns {
				@include pos(abs, null 30px 30px null);
				color: darken($white,40%);
				@include media-breakpoint-down(sm) {
					@include pos(static,null);
					margin-top: 10px; }
				a {
					&:hover {
						color: $lblue; } } } } } }
.btn-addr {
	@extend .button-square;
	display: inline-block;
	padding: 0px 20px;
	color: $white;
	&:hover {
		color: $white; } }
.cart-title {
	@include flexbox;
	align-items: center;
	margin-bottom: 15px;
	.icon {
		width: 45px; }
	.text {
		flex: 1; } }
.cart-list {
	.item {
		@include flexbox;
		border-bottom: 1px solid darken($white,10%);
		padding: 25px;
		@include transition(all,0.5s);
		@include media-breakpoint-down(sm) {
			flex-wrap: wrap;
			padding: 20px 0; }
		&:hover {
			box-shadow: 0px 5px 20px color-rgba($black,0.2); }
		.product {
			flex: 1;
			@include flexbox;
			@include media-breakpoint-down(sm) {
				width: 100%;
				flex: unset; }
			.img {
				width: 220px;
				height: 200px;
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				@include media-breakpoint-down(sm) {
					width: 80px;
					height: 80px; }
				img {
					max-width: 100%;
					max-height: 100%; } }
			.caption {
				flex: 1;
				@include media-breakpoint-down(sm) {
					padding-left: 15px; }
				.name {
					@include fs(16rem);
					line-height: 1.3;
					font-weight: 500;
					margin-bottom: 15px;
					@include media-breakpoint-down(sm) {
						@include fs(13rem);
						margin-bottom: 5px; } }
				.price {
					margin-bottom: 30px;
					@include media-breakpoint-down(sm) {
						margin-bottom: 5px; }
					.new {
						color: $yellow;
						@include fs(20rem);
						@include media-breakpoint-down(sm) {
							@include fs(16rem); } }
					.old {
						@include fs(15rem);
						color: darken($white,40%);
						text-decoration: line-through;
						@include media-breakpoint-down(sm) {
							@include fs(13rem); } } }
				.btn-remove {
					@include fs(24rem);
					color: darken($white,40%);
					@include media-breakpoint-down(sm) {
						@include fs(18rem); }
					&:hover {
						color: $lblue; } } } }
		.amount {
			width: 120px;
			@include flexbox;
			flex-direction: column;
			align-items: flex-end;
			@include media-breakpoint-down(sm) {
				flex-direction: row;
				align-items: center;
				padding-left: 95px; }
			select {
				border: 2px solid darken($white,5%);
				@include fs(16rem);
				font-weight: 500;
				color: $main;
				padding: 8px 15px;
				margin-bottom: 20px;
				@include media-breakpoint-down(sm) {
					@include fs(13rem);
					padding: 6px 10px;
					margin-bottom: 0;
					margin-right: 10px; } }
			.total {
				@include fs(18rem);
				font-weight: 500;
				@include media-breakpoint-down(sm) {
					@include fs(15rem); } } } } }
.cart-sidebar {
	background: #f9f9f9;
	padding: 20px;
	margin-bottom: 20px; }
.cart-promotion {
	margin-bottom: 30px;
	.form-group {
		&.coupon {
			@include pos(rel,null);
			.btn-submitcoupon {
				@include pos(abs, 4px 4px null null);
				background: #e1e1e1;
				color: darken($white,40%);
				padding: 8px 15px;
				line-height: 20px;
				@include fs(13rem);
				text-transform: uppercase;
				font-weight: 500;
				@include bor-rad(50px); } } } }
.cart-price {
	border-bottom: 1px solid darken($white,10%);
	margin-bottom: 15px;
	.group {
		@include flexbox;
		justify-content: space-between;
		@include fs(15rem);
		margin-bottom: 10px; } }
.cart-total {
	@include flexbox;
	.total {
		flex: 1;
		@include fs(14rem);
		line-height: 1.3;
		color: darken($white,40%);
		text-transform: uppercase;
		font-weight: 500;
		@include media-breakpoint-down(sm) {
			@include fs(13rem); }
		strong {
			color: $main;
			display: block;
			@include fs(24rem);
			text-transform: none;
			@include media-breakpoint-down(sm) {
				@include fs(16rem); } } }
	.btn-wrap {
		.btn-continue {
			@extend .button-square;
			line-height: 46px;
			padding: 0px 30px;
			box-shadow: 0px 3px 20px color-rgba($black,0.2);
			@include media-breakpoint-down(sm) {
				line-height: 36px;
				padding: 0px 15px; } } } }

.location-choice {
	margin-bottom: 20px;
	ul {
		@include flexbox;
		align-items: center;
		li {
			@include media-breakpoint-down(sm) {
				flex: 1; }
			+ li {
				margin-left: 10px; }
			a {
				display: block;
				background: #f2f2f2;
				min-width: 230px;
				padding: 0px 20px;
				text-align: center;
				line-height: 46px;
				@include fs(16rem);
				color: darken($white,40%);
				@include bor-rad(5px);
				@include media-breakpoint-down(sm) {
					line-height: 36px;
					padding: 0px 10px;
					min-width: 0;
					@include fs(13rem); }
				.fa {
					margin-right: 10px;
					@include media-breakpoint-down(sm) {
						margin-right: 5px; } } }
			&.active {
				a {
					color: $white;
					background: $dblue; } } } } }
.payment-info {
	@include pos(rel,null);
	.shipping-info {
		@include pos(abs, 5px 0 null null);
		@include media-breakpoint-down(lg) {
			@include pos(static,null);
			margin-top: -10px;
			margin-bottom: 20px; }
		ul {
			@include flexbox;
			flex-wrap: wrap;
			align-items: center;
			align-content: center;
			li {
				span {
					display: inline-block;
					vertical-align: middle;
					color: $lblue;
					padding: 1px 5px;
					margin: 0px 5px;
					@include bor-rad(50px);
					border: 1px solid darken($white,10%); } } } } }
.payment-list {
	.item {
		margin-bottom: 20px;
		.payment {
			padding: 30px;
			@include bor-rad(10px);
			@include pos(rel,null);
			border: 1px solid darken($white,10%);
			@include transition(all,0.5s);
			@include media-breakpoint-down(sm) {
				padding: 15px; }
			&:hover {
				border-color: transparent;
				box-shadow: 0px 5px 20px color-rgba($black,0.2); }
			.bank {
				@include pos(abs, 30px 30px null null);
				@include media-breakpoint-down(sm) {
					width: 70px;
					top: 15px;
					right: 15px; } }
			.name {
				@include fs(24rem);
				font-weight: 500;
				margin-bottom: 10px;
				@include media-breakpoint-down(sm) {
					@include fs(16rem); } }
			.phone {
				@include fs(18rem);
				margin-bottom: 10px;
				@include media-breakpoint-down(sm) {
					@include fs(14rem); } }
			.des {
				@include fs(14rem);
				color: darken($white,40%);
				text-transform: uppercase;
				@include media-breakpoint-down(sm) {
					@include fs(12rem); }
				span {
					@include fs(16rem);
					font-weight: 500;
					@include media-breakpoint-down(sm) {
						@include fs(14rem); } } }
			.default {
				display: inline-block;
				color: $lblue;
				@include fs(15rem);
				@include bor-rad(100px);
				border: 1px solid $lblue;
				padding: 0px 10px;
				margin-top: 10px; }
			.btns {
				@include pos(abs, null 30px 30px null);
				color: darken($white,40%);
				@include media-breakpoint-down(sm) {
					@include pos(static,null);
					margin-top: 10px; }
				a {
					&:hover {
						color: $lblue; } } } } } }
.btn-addcard {
	@extend .button-square;
	display: inline-block;
	padding: 0px 20px; }
